import { NgModule } from '@angular/core';
import {
  MatButtonModule,
  MatToolbarModule,
  MatCardModule,
  MatListModule,
  MatGridListModule,
  MatCheckboxModule,
  MatIconModule,
  MatInputModule

} from '@angular/material'


@NgModule({
  imports: [
    MatButtonModule,
    MatToolbarModule,
    MatCardModule,
    MatListModule,
    MatGridListModule,
    MatCheckboxModule,
    MatIconModule,
    MatInputModule
  ],
  exports: [
    MatButtonModule,
    MatToolbarModule,
    MatCardModule,
    MatListModule,
    MatGridListModule,
    MatCheckboxModule,
    MatIconModule,
    MatInputModule
  ]
})
export class MaterialModule { }


