import { Pipe, PipeTransform } from '@angular/core';
import { Todo } from './content/todo';

@Pipe({
  name: 'reverse'
  , pure: false
})
export class ReversePipe implements PipeTransform {

  transform(tickets: Todo[]){
    return tickets.reverse();
  }

}



